.alert-wrap {
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 9999;
  min-width: 250px;
}

service-whale-basket {
  position: relative;
  padding-top: 8px;
  display: block;
}

service-whale-basket .basket-wrap {
  position: absolute;
  right: 0;
  z-index: 999;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
}

service-whale-basket .basket {
  min-width: 110px;
  padding: 0;
}

service-whale-basket .basket .goods {
  list-style-type: none;
}

service-whale-cart-list {
  padding: 10;
}

service-whale-cart-list .thumbnail {
  padding: 10px;
  margin-bottom: 0;
}

service-whale-cart-list .thumbnail img {
  width: 150px;
  height: 150px;
}

service-whale-cart-list .media-body {
  padding-top: 20px;
  padding-left: 20px;
}

service-whale-cart-list .data-label {
  padding-top: 20px;
  margin-bottom: 15px;
}

service-whale-cart-list .price-label {
  font-size: 20px;
  padding: 10px 10px 10px;
}

service-whale-cart-list-filter .btn-data-filter {
  padding: 12px 15px;
}

service-whale-cart-list-filter .btn-color-select {
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 4px !important;
}

service-whale-rating .star-rating {
  padding: 0px;
}

service-whale-rating .star-rating .star {
  list-style-type: none;
  display: inline-block;
  padding-right: 2px;
}

